#mask {
  position: fixed;
  width: 0;
  height: 0;
  overflow: hidden;
  top: 0;
  opacity: 0;
  transition: opacity 0.2s;
}
#mask #mask-close {
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  cursor: pointer;
}
#mask #mask-close svg {
  width: 100%;
  height: 100%;
}
#mask img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#mask.enabled {
  width: 100%;
  height: 100%;
  padding-top: 6rem;
  padding-bottom: 5rem;
  z-index: 1000;
  opacity: 1;
}
#mask.closing {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s;
}
#mask a.open-tab {
  display: block;
  font-size: 1.2rem;
  text-align: center;
  text-overflow: ellipsis;
  padding: 10px;
  border-radius: 3px;
  transition: border-color 0.2s;
}
#mask a.open-tab svg {
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  vertical-align: middle;
  margin-left: 0.5rem;
}
#mask a.open-tab:hover {
  text-decoration: underline;
}

span.zoom {
  display: inline-block;
  position: relative;
  cursor: pointer;
}
span.zoom span.zoom-button {
  width: 4rem;
  height: 4rem;
  position: absolute;
  top: calc(50% - 2rem);
  left: calc(50% - 2rem);
  opacity: 0;
  transition: opacity 0.2s;
}
span.zoom span.zoom-button svg {
  width: 4rem;
  height: 4rem;
}
span.zoom img {
  transition: filter 0.2s;
}
span.zoom:hover span.zoom-button {
  opacity: 1;
}
span.zoom:hover img {
  filter: brightness(50%);
}

html.mask-enabled {
  height: 100%;
}

