}

html.js body > header {
  position: fixed;
  z-index: 500;
  transition: margin 0.3s;
}

main {
  flex: 1 0 0;
  padding-bottom: 6rem;
}
main > h1:first-child {
  margin-top: 1.6rem;
}
main > p {
