  padding-top: 4.8rem;
}

#main-menu-icon {
  display: none;
}

@media only screen and (max-width: 719px) {
  html.js body > header nav {
    display: none;
  }
  #main-menu-icon {
    display: block;
    padding: 0.8rem 1rem;
    font-family: "Open Sans", "sans serif";
    font-style: normal;
    font-weight: 400;
    font-size: 100%;
    border-radius: 0.3rem;
    line-height: 1;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  #main-menu-icon svg {
    width: 1.6rem;
    height: 1.6rem;
    margin-right: 0.6rem;
  }
  .slide-menu {
    position: fixed;
    z-index: 1001;
    text-transform: uppercase;
  }
  .slide-menu.left {
    top: 0;
    left: -100%;
    min-width: 75%;
    height: 100%;
    text-align: left;
    transition: left 0.4s;
  }
  .slide-menu.left.open {
    left: 0;
  }
  .slide-menu.right {
    top: 0;
    right: -100%;
    min-width: 75%;
    height: 100%;
    text-align: right;
    transition: right 0.4s;
  }
  .slide-menu.right.open {
    right: 0;
  }
  .slide-menu li {
    display: block;
    line-height: 1;
  }
  .slide-menu li a {
    display: block;
    padding: 1.6rem 1.8rem;
  }
  .menu-shadow {
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    z-index: 1000;
    opacity: 0;
    transition: opacity 0.4s;
  }
  body.menu-open .menu-shadow {
    width: 100%;
    height: 100%;
    opacity: 1;
  }
  body.menu-closing .menu-shadow {
    width: 100%;
    height: 100%;
  }
}
